"use client"
import { useEffect, useState } from 'react';
import ApiHandler from '@/utils/apiHandler';
import BannerCarousel from '@/components/Banner/Carousel'
import BookingForm from '@/components/booking-form'
import AboutSection from '@/components/about-section'
import RoomSection from '@/components/room-section'
import Gallery from '@/components/Gallery'
import BlogSection from '@/components/blog-section'
import ContactForm from '@/components/contact-form'

interface AboutData {
  title: string;
  description: string;
  image: string;
}

interface CarrouselImage {
  id: number;
  src: string;
  alt: string;
}

export default function Home() {
  const [aboutData, setAboutData] = useState<AboutData | null>(null);
  const [carrouselImages, setCarrouselImages] = useState<CarrouselImage[]>([]);
  
  const apiHandler = ApiHandler({ baseUrl: process.env.NEXT_PUBLIC_API_URL || '' });

  useEffect(() => {
    const fetchAboutData = async () => {
      try {
        const data = await apiHandler.fetchData('about?populate=*');
        setAboutData({
          title: data.data.title,
          description: data.data.blocks[1].body,
          image: data.data.blocks[0].url
        });
      } catch (error) {
        console.error('Error fetching about data:', error);
      }
    };

    const fetchCarrouselImages = async () => {
      try {
        const data = await apiHandler.fetchData('gallery'); // Replace with your actual endpoint for gallery images
        const formattedImages = data.map((item: any) => ({
          id: item.id,
          image: item.image, // Keep 'image' to match the updated GalleryImage interface
          alt: item.alt || `Gallery image ${item.id}` // Default alt text if not provided
        }));
        setCarrouselImages(formattedImages);
      } catch (error) {
        console.error('Error fetching gallery images:', error);
      }
    };

    fetchAboutData();
    fetchCarrouselImages();
  }, []);

  return (
    <>
      {/* Banner Section with Carousel */}
      <section className="banner_main">
        <BannerCarousel images={carrouselImages} />
        <div className="booking_ocline">
          <div className="container">
            <div className="row">
              <div className="col-md-5">
                <BookingForm />
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* About Section */}
      {aboutData && typeof aboutData === 'object' && !Array.isArray(aboutData) && <AboutSection {...aboutData} />}

      {/* Room Section */}
      <RoomSection rooms={[]} />

      {/* Gallery Section */}
      <Gallery images={galleryImages} />

      {/* Blog Section */}
      <BlogSection posts={[]} />

      {/* Contact Section */}
      <ContactForm />
    </>
  );
}
 